// Настройки ===============================================================================================================================================================================================================================================================================================
@import "_base/settings";
@import "_base/components";
@import "_base/components/items";

body {
	// background-color: red;
}

.header {

	&__row1 {
		background: #484848;
		color: #fff;
		font-size: 13px;

		.container {
			display: flex;
			align-items: center;
			min-height: 31px;
			justify-content: space-between;
			ul {
				display: flex;
				gap: 20px;
			}
		}
	}

	&__row2 {
		border-bottom: 1px solid #DFDFDF;
		.container {
			display: flex;
			align-items: center;
			min-height: 84px;

		}

		.btn {
			margin-left: 26px;
			min-height: 50px;
			gap: 13px;
			display: flex;
			align-items: center;
			padding: 0 20px;
			font-weight: 400;
		}

		&-search {
			height: 50px;
			margin-left: 18px;
			display: flex;
			flex-grow: 1;
			.placeholder-container {
				width: 100%;
				color: #CCC;
				position: relative;
				.btn {
					position: absolute;
					top: 5px;
					right: 5px;
					width: 40px;
					min-height: 40px;
					max-height: 40px;
					z-index: 2;
					border-radius: 3px;
					display: flex;
					align-items: center;
					justify-content: center;
					&::before {
						display: block;
						min-width: 19px;
						min-height: 19px;
						background: url("../img/icon/search.svg");
						content: '';

					}

				}
			}
		}

		&-right {
			min-height: 50px;
			margin-left: 56px;
			display: flex;
			gap: 32px;
			font-size: 13px;
			font-weight: 400;
			color: #494F54;

			&-price {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				&-caption {
					background: #B4DFFB;
					border-radius: 3px;
					font-size: 11px;
					font-weight: 700;
					line-height: lh(11, 22);
					text-align: center;
					position: relative;
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						background: #FB1A1A;
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;

						border-radius: 100%;
					}
				}
				p {
					line-height: 1;
				}

			}

			&-favourites {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 21px;
						height: 18px;
						background: url("../img/icon/heart.svg");
						content: '';
					}
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						background: #FB1A1A;
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;
						border-radius: 100%;
					}
				}
				p {
					line-height: 1;
				}
			}

			&-user {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 19px;
						height: 21px;
						background: url("../img/icon/user.svg");
						content: '';
					}
				}
				p {
					line-height: 1;
				}
			}

			&-cart {
				position: relative;
				cursor: pointer;
				align-self: flex-start;

				&-caption {
					padding: 0 9px;
					background: #3E4347;
					border-radius: 3px 3px 0 0;
					color: #fff;
					font-size: 11px;
					font-weight: 700;
					line-height: lh(12, 22);
					display: flex;
					align-items: center;
					gap: 19px;

					&::after {

						line-height: 1;
						position: relative;
						width: 11px;
						height: 7px;
						background: url("../img/icon/arr/arr.svg");
						content: '';
						transform: rotate(180deg);
						transition: .3s;
					}

					&:hover {

						&::after {
							transform: rotate(0);
						}

						& + .popup-cart-list {
							transform: rotateX(0);
							visibility: visible;
							opacity: 1;
						}
					}
				}

				.popup-cart {
					position: absolute;
					right: 0;
					background: #FFFFFF;
					border: 1px solid #DBDBDB;
					box-shadow: 0 0 35px 1px rgba(0, 0, 0, 0.15);
					border-radius: 5px 0 5px 5px;
					width: 250px;
					font-size: 13px;
					line-height: lh(13, 22);
					color: #333;
					z-index: 5;

					// корзина список
					&-list {
						transform: rotateX(-90deg);
						transform-origin: 0 0;
						transition: transform .3s linear;
						visibility: hidden;
						opacity: 0;

						&__item {
							display: flex;
							align-items: center;
							gap: 14px;
							transition: all .5s;
							cursor: pointer;


							.checkbox {
								max-width: 175px;
								overflow: hidden;
								flex-grow: 1;

								label {
									white-space: nowrap;
									padding: 11px;
									transition: all 1s;
									&::before {
										min-width: 29px;
										height: 16px;
										background-color: var(--cart-color);
										border: none;
									}

								}

								// при checked
								input:checked + label:before {
									background-image: url("../img/icon/arr/chec.svg");
									background-repeat: no-repeat;
									background-position: center center;
								}

							}

							&-edit {
								min-width: 14px;
								min-height: 14px;
								max-width: 14px;
								max-height: 14px;
								background: url("../img/icon/pen.svg");
							}

							&-del {
								min-width: 16px;
								min-height: 16px;
								max-width: 16px;
								max-height: 16px;
								background: url("../img/icon/del.svg");

							}

							&-create {
								padding: 7px 11px 11px 11px;
								display: flex;
								align-items: center;
								gap: 18px;
								&::before {
									width: 21px;
									height: 21px;
									background: url("../img/icon/plus.svg");
									content: '';
								}

							}

							&:hover {
								background-color: #F1F1F1;
							}

						}
					}

					// корзина удалить
					&-del {
						width: 250px;
						padding: 22px 25px;
						text-align: center;
						opacity: 0;

						h3 {
							margin-bottom: 22px;
						}

						.checkbox {
							max-width: 175px;
							overflow: hidden;
							flex-grow: 1;
							margin-bottom: 36px;

							label {
								white-space: nowrap;
								padding: 11px;
								transition: all 1s;
								&::before {
									min-width: 29px;
									height: 16px;
									background-color: var(--cart-color);
									border: none;
								}

							}

							// при checked
							input:checked + label:before {
								background-image: url("../img/icon/arr/chec.svg");
								background-repeat: no-repeat;
								background-position: center center;
							}

						}

						&-btns {
							display: flex;
							justify-content: space-between;
							.btn {
								display: flex;
								justify-content: center;
								margin: 0;
								width: 67px;
								min-height: 37px;
								color: #fff;
							}
						}

						&.open {
							opacity: 1;
						}
					}

					// создать новую корзину
					&-create {
						width: 477px;
						padding: 26px 70px 40px;
						text-align: center;
						opacity: 0                 ;

						h3 {
							margin-bottom: 22px;
						}

						.checkbox {
							margin-bottom: 36px;
							width: 30px;

							label {
								transition: all 1s;
								&::before {
									min-width: 30px;
									height: 30px;
									background-color: var(--cart-color);
									border: none;
								}

							}

							// при checked
							input:checked + label:before {
								background-image: url("../img/icon/arr/check-large.svg");
								background-repeat: no-repeat;
								background-position: center center;
							}

						}

						&-check {
							display: flex;
							padding: 0 27px;
							justify-content: space-between;
						}

						&-btns {
							display: flex;
							justify-content: space-between;
							gap: 25px;
							.btn {
								display: flex;
								justify-content: center;
								margin: 0;
								width: 149px;
								min-height: 37px;
							}
						}

						&.open {
							opacity: 1;
						}
					}




				}





			}




		}

	}

	&__row3 {
		align-items: center;
		margin-top: 20px;
		margin-bottom: 45px;
		.container {
			display: flex;
			gap: 22px;
		}

		.btn {
			height: 47px;
			padding: 0 26px 0 13px;
			gap: 10px;
			align-items: center;
			border-radius: 10px;
			display: inline-flex;
			&--spes {
				background-color: #FFCB00;
				&::before {
					width: 23px;
					height: 23px;
					background: url("../img/icon/money.svg");
					content: '';
				}

			}
			&--sale {
				background-color: #42ADE2;
				&::before {
					width: 25px;
					height: 34px;
					background: url("../img/icon/lightning.svg");
					content: '';
				}
			}
			&--new {
				background-color: #C7E755;
				&::before {
					width: 23px;
					height: 25px;
					background: url("../img/icon/siren.svg");
					content: '';
				}
			}
			&--ball {
				background-color: #B4DFFB;
				position: relative;
				span {
					position: absolute;
					top: -8px;
					right: -24px;
					background: #FF5656;
					border-radius: 15px;
					color: #fff;
					font-size: 14px;
					line-height: lh(14, 22);
					padding: 0 10px;
					display: block;
				}
				&::before {
					width: 32px;
					height: 32px;
					background: url("../img/icon/ball.svg");
					content: '';
				}
			}
		}
	}

}

.main-slider {
	background-color: #F8F8F8;
	padding-top: 20px;
	padding-bottom: 40px;
	margin-bottom: 100px;

}

.hits-slider {
	padding-bottom: 133px;
	h2 {
		margin-bottom: 50px;
		display: inline-flex;
		align-items: center;
		span {
			margin: 0 4px;
			position: relative;
			&::before {
			    position: absolute;
			    top: 50%;
				transform: translatey(-30%) rotate(-4deg);
				left: 0;
				width: 100%;
				height: 3px;
				background-color: #E43535;
				content: '';
			}
		}
		&::after {
			width: 43px;
			height: 48px;
			background: url("../img/icon/fire.svg");
			content: '';
			margin-top: -10px;
		}

	}

	&__inner {
		position: relative;
	}

	&__box {
		max-width: 1291px;
		margin: 0 auto;
	}

	// pagination arr
	&-navigation {
		position: absolute;
		top: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 215px;
		// arr
		&__prev, &__next {
			background: #F4F4F4;
			border: 1px solid #E7E7E7;
			box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
			width: 45px;
			height: 45px;
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 100%;
			&::before {
				width: 11px;
				height: 19px;
				background: url("../img/icon/arr/arr-slider.svg");
				content: '';
			}

			// если далее нету слайдов
			&.swiper-button-disabled,
			&.swiper-button-disabled {
				opacity: 0;
				pointer-events: none;
			}
		}

		&__next {
			&::before {
				transform: rotate(-180deg);
			}
		}


	}
}

.water-season {
	margin-bottom: 150px;
	h2 {
		display: flex;
		align-items: center;
		gap: 24px;
		height: 85px;
		margin-bottom: 50px;
		font-size: 48px;
		background-image: linear-gradient(90deg, #015078 0%, #0276B4 100%);
		color: transparent;
		background-clip: text;
		&::after {
			width: 104px;
			height: 85px;
			background: url("../img/icon/swimmer.png");
			content: '';
		}
	}

	&__body {
		background: url("../img/bg-water.jpg") center center/cover no-repeat;
		background-size: 100%;
		padding: 34px;

		&-list {
			display: flex;
			justify-content: space-between;
			margin-bottom: 36px;
			.item-product {
				padding: 0 37px 30px;
				flex-basis: 316px;
				background-color: #fff;
				border-radius: 15px;

				&-img-box {
					height: 242px;
				}

				&-price-box {
					display: flex;
					font-size: 12px;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 23px;

					&__sale {
						display: flex;
						justify-content: space-between;
						min-height: 30px;
						span {
							display: flex;
							align-items: center;
							gap: 6px;
							background: #339933;
							border-radius: 5px;
							padding: 0 7px;
							font-size: 14px;
							color: #fff;
						}
					}

				}

				&-text {
					padding: 0;
					margin-bottom: 20px;

				}

				&-stock {
					justify-content: flex-start;
					gap: 9px;
					margin-bottom: 20px;

				}

				&-cart {
					display: flex;
					justify-content: space-between;
					input {
						padding-left: 20px;
						width: 90px;
						border: 1px solid #CDCDCD;
						border-radius: 5px;

					}

				}
			}

		}

		&-btn {
			font-weight: 700;
			font-size: 30px;
			line-height: 36px;
			padding: 10px 73px;
			position: relative;
			z-index: 1;
			background-color: transparent;
			border: none;
			display: flex;
			min-width: 354px;
			margin: 0 auto;


			&::after {
				position: absolute;
				z-index: -1;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: #fff;
				border-radius: 27.5px;
				content: '';
				transition: all 1s;
			}
			&::before {
			    position: absolute;
				z-index: -1;
			    top: 10px;
				left: 0;
				width: 100%;
				height: 100%;
				background: #00D1FF;
				border-radius: 27.5px;
				content: '';
			}

			&:hover{
				&:after {
					top: 10px;
				}

			}

		}
	}




}

.recomended-season {
