.footer {
	padding: 35px 0 45px;
	background: #484848;
	font-weight: 400;
	font-size: 14px;
	line-height: lh(14, 30);
	color: #EAEAEA;
	&-row1 {
		display: flex;
		justify-content: space-between;
		h3 {
			font-weight: 500;
			font-size: 16px;
			line-height: lh(16, 22);
			margin-bottom: 12px;
		}
	}
}


.page-basket {
	padding: 20px;

	&-title {
		padding-bottom: 50px;
		display: flex;
		align-items: flex-end;
		gap: 20px
	}

	&-tab {
		.tab__nav {
			display: flex;
			gap: 42px;
			padding-bottom: 20px;
			font-size: 16px;
			font-weight: 400;
		}
		.tab__link {
			min-width: 150px;
			min-height: 45px;
			display: flex;
			align-items: center;
			justify-content: center;
			&--active {
				color: #000;
				background: #ECECEC;
				border-radius: 5px;
				border-bottom: 3px solid red;
			}
		}
		.tab__content-item {
			display: flex;
			gap: 40px;
		}


		&__left {
			flex: 1 1 auto;
			&-selected-all {
				margin-bottom: 30px;
				min-height: 38px;
				border-bottom: 1px solid #EAEAEA;
			}


		}

		&__right {
			position: sticky;
			top: 20px;
			width: 317px;
			height: 100px;

			.btn {
				font-size: 16px;
				font-weight: 500;
				width: 100%;
			 	height: 65px;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-bottom: 25px;
			}

			&-spec {
				border: 1px solid #EAEAEA;
				background: linear-gradient(0deg, #EAEAEA, #EAEAEA),
				linear-gradient(0deg, #F4F4F4, #F4F4F4);
				padding: 20px;
				border-radius: 15px;
				margin-bottom: 25px;


				&__li {
					font-size: 14px;
					line-height: lh(14,30);
				}

				&__total {
					display: flex;
					justify-content: space-between;
					align-items: flex-end;
					margin-top: 12px;
					border-top: 1px solid #D0D0D0;
					padding-top: 17px;
					font-size: 20px;
					span {
						font-size: 24px;
						font-weight: 700;
					}

				}
			}

			&-info {
				background: #D0EDFF;
				padding: 20px;
				border-radius: 15px;
				margin-bottom: 25px;
				font-size: 12px;
				line-height: lh(12,24);
			}

		}

	}


}

.page-catalog-list {

	.top-part {
		.banner {
			margin-bottom: 55px;
		}
		.breadcrumps {
			margin-bottom: 50px;

			a:first-child {
				display: inline-flex;
				gap: 4px;
				margin-right: 20px;
				font-size: 22px;
				font-weight: 600;
				&::after {
					width: 28px;
					height: 28px;
					background: url("../img/icon/arr/arr-breadcrumps.svg");
					content: '';
				}

			}

			.select {
				&__header {
					border-radius: 5px;
					border: 1px solid #999999;
					&::before {
						width: 14px;
						height: 12px;
						background: url("../img/icon/menu.svg");
						content: '';
					}
				}
				&__body {
					width: 100%;
					border-radius: 5px;
				}

			}
		}

	}

	.top-bar {
		display: flex;
		align-items: center;
		margin-bottom: 30px;

		&__find{
			color: #666;
		}

		.checkbox {
			margin-left: auto;
			span {
				//width: 20px;
				//height: 20px;

			}
			span {
				gap: 8px;
				&:before {
					width: 20px;
					height: 20px;
					border-radius: 3px;
				}

			}

		}

		.select {
			margin-left: 20px;
			&__header {
				border-radius: 5px;
				border: 1px solid #999999;
				&::before {
					width: 15px;
					height: 15px;
					background: url("../img/icon/top-bar/ruble.svg");
					content: '';
				}
			}
			&__body {
				width: 100%;
				border-radius: 5px;
			}

			&--sort {
				min-width: 171px;
				.select__header {
					width: 100%;
					&::before {
						width: 19px;
						height: 19px;
						background: url("../img/icon/top-bar/sort.svg");
						content: '';
					}
				}
			}

		}

		&__select-view {
			display: flex;
			gap: 12px;
			align-items: center;
			margin-left: 32px;
			&__column {
				width: 18px;
				height: 18px;
				background: url("../img/icon/top-bar/column.svg");
			}

			&__row {
				width: 21px;
				height: 15px;
				background: url("../img/icon/top-bar/row.svg");
			}

		}


	}

	.body {
		display: flex;
		gap: 20px;
		&__filter {
			width: 230px;
			h3 {
				font-size: 14px;
				font-weight: 600;
				line-height: lh(14,26);
				margin-bottom: 9px;
			}

			.checkbox {
				display: block;
				margin-bottom: 12px;
				span {
					gap: 8px;
					&:before {
						width: 20px;
						height: 20px;
						border-radius: 3px;
					}

				}

			}

			.select {
				width: 100%;
				margin-bottom: 12px;

				&__header {
					width: 100%;
					justify-content: space-between;
					border-radius: 5px;
					color: #999;
					border: 1px solid #999999;
					&::after {
						width: 17px;
						height: 9px;
						background: url("../img/icon/arr/arr-filter.svg");
						content: '';
					}
				}
				&__body {
					width: 100%;
					border-radius: 5px;
					opacity: 1;
					padding: 20px 21px 0 21px;
					&::after {
						min-width: 17px;
						height: 9px;
						background: url("../img/icon/arr/arr-filter.svg");
						content: '';
					}
					&-item {
						border-bottom: 1px solid #CDCDCD;
						margin-bottom: 11px;
						padding: 0 0 11px 0;
						display: block;
						&:last-child {
							padding: 0;
							border: none;
						}

						span {
							pointer-events: none;
							display: flex;
							align-items: center;
							gap: 10px;

							&:before {
								transition: all .5s;
								border: 1px solid #CDCDCD;
								border-radius: 3px;
								min-width: 20px;
								height: 20px;
								content: '';
							}

						}
					}
				}



			}
		}
		&__list {

		}

	}

}










