@import url("https://fonts.googleapis.com/css?family=Rubik:700&display=swap");
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: 0.5s all;
  cursor: pointer;
}
a:hover {
  transition: all 0.5s;
  opacity: 0.5;
}

path {
  transition: 0.5s all;
}

[class*=btn] {
  cursor: pointer;
}
[class*=btn]:hover {
  opacity: 0.9;
}

html {
  height: 100%;
}
html body {
