  line-height: 1.1428571429;
}
html body > main {
  flex: 1 1 auto;
}
html .container {
  max-width: 1430px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.placeholder-container {
  position: relative;
}
.placeholder-container input, .placeholder-container textarea {
  color: inherit;
  width: 100%;
  height: 50px;
  position: relative;
  z-index: 2;
  padding: 20px 14px;
  border: 1px solid #CDCDCD;
  border-radius: 5px;
}
.placeholder-container input:focus, .placeholder-container textarea:focus {
  outline: 0;
}
.placeholder-container input:focus + .caption, .placeholder-container input:not(:placeholder-shown) + .caption, .placeholder-container textarea:focus + .caption, .placeholder-container textarea:not(:placeholder-shown) + .caption {
  opacity: 1;
  top: 0;
  padding: 0 15px;
  z-index: 3;
  transform: translate(0, -50%);
  color: #FFB444;
  background-color: #fff;
}
.placeholder-container .caption {
  position: absolute;
  transition: all 1s;
  top: 50%;
  left: 20px;
  z-index: 2;
  transform: translate(0, -50%);
  pointer-events: none;
}
.placeholder-container .error {
  border-bottom: 1px solid #FFCB00 !important;
  border-radius: inherit;
}
.placeholder-container__textarea {
  height: 200px;
}

.checkbox {
  position: relative;
}
.checkbox__input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}
.checkbox__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  line-height: 1;
}
.checkbox__label::before {
  width: 25px;
  height: 25px;
  border: 1px solid #CDCDCD;
  border-radius: 3px;
  content: "";
  transition: all 0.3s;
}
.checkbox__input:checked + .checkbox__label:before {
  background: url("../img/icon/arr/check-black.svg") no-repeat center center;
}

label.checkbox {
  cursor: pointer;
}
label.checkbox input {
  pointer-events: none;
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}
label.checkbox input:checked + span:before {
  background: url("../img/icon/arr/check-black.svg") no-repeat center center;
}
label.checkbox span {
  pointer-events: none;
  display: flex;
  align-items: center;
  gap: 10px;
}
label.checkbox span:before {
  transition: all 0.5s;
  border: 1px solid #CDCDCD;
  width: 25px;
  height: 25px;
  content: "";
}

.radio {
  position: relative;
  padding: 4px 0;
}
.radio__input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}
.radio__label {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.radio__label::before {
  width: 23px;
  height: 23px;
  border: 1px solid #CFCFCF;
  content: "";
  margin-right: 10px;
  transition: all 0.3s;
  border-radius: 50%;
}
.radio__input:checked + .radio__label:before {
  background-color: #FFCB00;
}

.btn {
  background-color: #FFB444;
  font-size: 16px;
  padding: 7px 8px;
  cursor: pointer;
  transition: 1s;
  border: none;
  border-radius: 5px;
  text-align: center;
  line-height: 1;
  font-weight: 600;
}
.btn:hover {
  opacity: 0.6;
}
.btn--red {
  background-color: #E43535;
}
.btn--green {
  background-color: #339933;
}
.btn--silver {
  background-color: #D6D6D6;
}

.select {
  position: relative;
  font-size: 12px;
  display: inline-flex;
}
.select.is-active .select__body {
  transform: rotateX(0);
  visibility: visible;
  opacity: 1;
  transition: transform 0.3s linear;
}
.select__header {
  position: relative;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  padding: 0 10px 0 7px;
  border-radius: 8px;
  border: 1px solid #9A9A9A;
  min-width: 79px;
  height: 28px;
  gap: 10px;
}
.select__header span {
  position: absolute;
  font-size: 11px;
  top: 0;
  left: 27px;
  transform: translateY(-60%);
  background-color: #fff;
}
.select__header::after {
  min-width: 10px;
  height: 6px;
  background: url("../img/icon/arr/arr-select.svg");
  content: "";
}
.select__body {
  padding: 14px 0;
  background-color: #fff;
  border: 1px solid #cccccc;
  border-radius: 8px;
  left: 0;
  position: absolute;
  top: 0;
  width: 140px;
  z-index: 5;
  transform: rotateX(-90deg);
  transform-origin: 0 0;
  transition: transform 0.3s linear;
  visibility: hidden;
  opacity: 0;
}
.select__body::after {
  position: absolute;
  top: 10px;
  right: 10px;
  min-width: 10px;
  height: 6px;
  background: url("../img/icon/arr/arr-select.svg");
  content: "";
  transform: rotate(-180deg);
  cursor: pointer;
}
.select__body input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}
.select__body-item {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 7px 14px;
  width: 100%;
}
.select__body-item span {
  width: 130px;
}
.select__body-item.active::after {
  min-width: 11px;
  height: 8px;
  background: url("../img/icon/arr/arr-select-green.svg");
  content: "";
}
.select__body-item:hover {
  background-color: #B4DFFB;
}

.modal-popup {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}
.modal-popup::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
.modal-popup [class*=__container] {
  position: relative;
  width: 500px;
  padding: 30px;
  margin: 50px auto;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 10px;
  cursor: default;
}
@media (max-width: 1024px) {
  .modal-popup [class*=__container] {
    width: 90%;
  }
}
.modal-popup__close {
  font-size: 50px;
  font-weight: bold;
  color: #FFCB00;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.modal-popup.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}
.modal-popup__container.modal-popup-open {
  display: inline-block;
}
.modal-popup .fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}
.modal-popup .fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}
.modal-popup .fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity var(--transition-time), transform var(--transition-time);
}
.modal-popup .fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-time), transform var(--transition-time);
}
.modal-popup .form-modal {
  text-align: center;
}
.modal-popup .form-modal__title {
  margin-bottom: 20px;
}
.modal-popup .form-modal .btn {
  margin: 0 auto;
}

.disable-scroll {
