.item-product {
	padding-top: 12px;
	padding-left: 12px;

	&-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		span {
			display: flex;
			align-items: center;
			position: absolute;
			background-color: #FF2727;
			color: #fff;
			font-weight: 700;
			padding: 0 15px;
			top: -12px;
			left: -12px;
			z-index: 10;
			border-radius: 5px;
			height: 30px;
		}
		margin-bottom: 3px;
	}

	&-articale {
		font-size: 12px;
		line-height: lh(12,26);
		color: #666;
		span {
			color: #999;
		}

		margin-bottom: 9px;
	}

	&-text {
		padding-left: 20px;
		margin-bottom: 4px;
		min-height: 60px;
	}

	&-price-box {
		font-size: 16px;
		line-height: lh(16,26);
		margin-bottom: 9px;
		.price-old {
			color: #666;
			padding: 0 4px;
			position: relative;
			&::before {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 100%;
				height: 1px;
				background-color: #666;
				content: '';
			}

			&--red {
				&::before {
					background-color: #ED4C5C;
				}
			}
		}

		strong {
			margin-left: 7px;
		}

		.border {
			background: #339933;
			color: #fff;
			padding: 3px 7px;
			border-radius: 3px;
		}
	}

	&-stock {
		font-size: 14px;
		line-height: lh(14,16);
		margin-bottom: 13px;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 5px;
		&::before {
			width: 22px;
			height: 22px;
			background: url("../img/icon/arr/arr-green.svg");
			content: '';
		}

	}

	&-sel {
		display: flex;
		align-items: center;
		justify-content: space-between;
		input {
			width: 56px;
			height: 40px;
			border: 1px solid #CDCDCD;
			border-radius: 5px;
			padding: 0 5px 0 15px;
		}
		&-basket {
			width: 75px;
			height: 40px;
			max-height: 40px;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			&::before {
				width: 30px;
				height: 29px;
				background: url("../img/icon/cart_black.svg");
				content: '';
			}
			&.orange {
				background-color: transparent;
				border: 2px solid #FFB444;
				&::before {
					background: url("../img/icon/cart_orange.svg");
				}
			}
		}

		&-cart {
			height: 40px;
			max-height: 40px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 16px;
			padding: 0 18px;

		}


		&-favorites {
			width: 19px;
			height: 17px;
			background: url("../img/icon/heart-hit.svg");
			&.orange {
				background: url("../img/icon/heart-orahge.svg");
			}
		}
		&-bar {
			width: 14px;
			height: 18px;
			background: url("../img/icon/bar-hit.svg");
			&.orange {
				background: url("../img/icon/bar-orange.png");
			}
		}
	}
}

.item-product-line {
	border-bottom: 1px solid #EAEAEA;
	display: flex;
	padding: 20px 0;

	&__img-box {

		margin-left: 20px;
		width: 140px;
		height: 140px;
		display: flex;
		align-items: center;
		justify-content: center;

	}

	&__descr-box {
		margin-left: 48px;
		margin-right: 40px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		&__text {
			font-size: 16px;
			line-height: lh(16,26);
		}

		&__artical {
			color: #666;
			span {
				color: #999;
			}
		}

		&__stock-box {
			min-height: 26px;
			display: flex;
			align-items: center;

			&__quantity {
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 5px;
				&::before {
					width: 22px;
					height: 22px;
					background: url("../img/icon/arr/arr-green.svg");
					content: '';
				}

			}

			&__favorites {
				width: 19px;
				height: 17px;
				background: url(../img/icon/heart-hit.svg);
				margin-left: auto;
				cursor: pointer;
				transition: all .5s;
				&:hover {
					background: url(../img/icon/heart-orahge.svg);
				}
			}
			&__bar {
				margin-left: 20px;
				width: 14px;
				height: 18px;
				background: url(../img/icon/bar-hit.svg);
				cursor: pointer;
				transition: all .5s;
				&:hover {
					background: url(../img/icon/bar-orange.png);
				}
			}
		}

	}


	&__spec-box {
		margin-left: auto;
		min-width: 163px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		&__price {
			margin-bottom: 20px;
			display: flex;
			gap: 25px;
			&-text {
				font-weight: 700;
				font-size: 20px;
				width: 82px;
			}
		}

		&__quan-box {
			height: 40px;
			margin-bottom: 15px;
			display: flex;
			align-items: center;
			gap: 25px;
			input {
				width: 82px;

				font-weight: 400;
				font-size: 16px;
				padding-left: 13px;
				height: 100%;
				border: 1px solid #CDCDCD;
				border-radius: 5px;
			}

			&__basket {

				width: 30px;
				height: 30px;
				background: url("../img/icon/del-grey.svg");
			}

			.btn {
				font-weight: 500;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 40px;
				width: 120px;
			}

		}

		&__unit {
			font-size: 13px;
			line-height: lh(13,18);
		}
	}

}
