// custom шрифт
@mixin font-face($font-name, $path, $weight: normal, $style: normal) {
	@font-face {
		font-family: quote($font-name);
		src:
				url(#{$path}.woff) format("woff"),
				url(#{$path}.woff2) format("woff2");
		font-weight: $weight;
		font-display: swap;
		font-style: $style;
	}
}
