@import url('https://fonts.googleapis.com/css?family=Rubik:700&display=swap');

@mixin font-default($font: false) {
	@if $font {
		font-family: $font, sans-serif;
	} @else {
		font-family: 'Noto Sans', sans-serif;
		font-weight: 500;
		font-size: 14px;
		color: #000;
		line-height: lh(14, 16);
	}
}


